.advantages {
    padding: 50px 20px 60px;
    text-align: center;

    @media (min-width: $breakpoint-medium) {
        padding-top: 60px;
        padding-bottom: 100px;
    }

    @media (min-width: $breakpoint-large) {
        padding: 100px 40px 120px;
    }
}

.advantages__header {
    margin: 0;
    margin-bottom: 36px;
    font-size: 24px;
    line-height: 36px;
    font-weight: 700;

    @media (min-width: $breakpoint-medium) {
        margin-bottom: 60px;
        font-size: 42px;
        line-height: 64px;
    }

    @media (min-width: $breakpoint-large) {
        margin-bottom: 80px;
    }
}

.advantages__title {
    margin: 10px 0 4px;
    font-size: 18px;
    line-height: 32px;
    font-weight: 700;

    @media (min-width: $breakpoint-medium) {
        margin-top: 0;
        margin-bottom: 10px;
        font-size: 24px;
        line-height: 36px;
    }

    @media (min-width: $breakpoint-large) {
        margin-top: 20px;
    }
}

.advantages__description {
    margin: 0;

    @media (min-width: $breakpoint-medium) and (max-width: $breakpoint-large) {
        grid-column: 2 / 3;
    }

    @media (min-width: $breakpoint-large) {
    
    }
}

.advantages__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    text-align: center;

    @media (min-width: $breakpoint-medium) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
        row-gap: 50px;
        text-align: left;
    }

    @media (min-width: $breakpoint-large) {
        grid-template-columns: repeat(4, 1fr);
        text-align: center;
    }
}

.advantages__item {
    display: grid;
    grid-template-rows: 56px auto auto;

    @media (min-width: $breakpoint-medium) and (max-width: $breakpoint-large) {
        grid-template-columns: 56px 1fr;
        column-gap: 20px;
    }

    & + & {
        @media (max-width: $breakpoint-small-down) {
            margin-top: 32px;
        }
    }

    &::before {
        content: "";
        width: 52px;
        height: 56px;
        justify-self: center;
        background-repeat: no-repeat;
        background-position: 50% 50%;
    }
}

.advantages__item--speed::before {
    background-image: url("../img/icons/watch.svg");
}

.advantages__item--fit::before {
    background-image: url("../img/icons/pocket.svg");
}

.advantages__item--energy::before {
    background-image: url("../img/icons/lightning.svg");
}

.advantages__item--eco::before {
    background-image: url("../img/icons/leaf.svg");
}