.peppermint {
    overflow: hidden;
}

.promo {
    padding: 20px 0 60px;
    text-align: center;
    background-color: $bgr-flat-white;
    overflow: hidden;
}

.peppermint-slides {
    display: flex;
}

.promo__container {
    position: relative;
    display: grid;
}

.promo__btns {
    position: absolute;
    top: 50%;
    display: flex;
    justify-content: space-between;
    width: 100%;
    transform: translateY(-50%);
}

.promo__item {
    @media (max-width: $breakpoint-large) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    @media (min-width: $breakpoint-large) {
        display: grid;
        grid-template-columns: 600px 650px;
        column-gap: 30px;
        align-items: center;
    }
}

.promo__content {
    @media (min-width: $breakpoint-large) {
        text-align: left;
    }
}

.promo__picture,
.promo__image {
    width: 280px;

    @media (min-width: $breakpoint-medium) {
        order: 2;
        width: 476px;
        height: 476px;
    }

    @media (min-width: $breakpoint-large) {
        width: 600px;
        height: 600px;
        order: 0;
    }
}

.promo__header {
    margin: 0;
    margin-bottom: 10px;
    font-size: 36px;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: 2px;

    @media (min-width: $breakpoint-medium) {
        margin-bottom: 16px;
        font-size: 56px;
        line-height: 80px;
    }
}

.promo__text {
    margin: 0;
    margin-bottom: 20px;

    @media (min-width: $breakpoint-medium) {
        font-size: 20px;
        line-height: 32px;
    }

    @media (min-width: $breakpoint-large) {
        margin-bottom: 40px;
    }
}

.promo__old-price {
    font-size: 16px;
    line-height: 32px;
    color: $color-lilac;
}

.promo__new-price {
    font-size: 20px;
    line-height: 32px;
    font-weight: 700;
}

.promo__btn {
    width: 80px;
    color: $color-lilac;
    background-color: transparent;
    border: solid 1px red;
    cursor: pointer;
    background-image: url("../img/icons/arrow.svg");
    background-position: center;
    background-repeat: no-repeat;
}

.promo__btn--next {
    transform: rotate(180deg);
}

.promo__toggles, .peppermint-dots {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.promo__toggle, .peppermint-dots > li {
    width: 62px;
    height: 4px;
    margin: 0 4px;
    padding: 0;
    background-color: $color-white;
    border: none;
    cursor: pointer;
}

.promo__toggle--current, .peppermint-dots > .peppermint-active-dot {
    background-color: $color-lilac;
}

.slider {
    display: flex;

    @media (max-width: $breakpoint-small-down) {
        max-width: 320px;
    }

    @media (min-width: $breakpoint-small-down) {
        max-width: 1360px;
    }
}