.control {
  position: relative;
  z-index: 0;
  display: grid;
  gap: 10px;
  font-size: 16px;
  line-height: 24px;
  cursor: pointer;
}

.control--radio {
  grid-template-columns: 24px 1fr;
}

.control--checkbox {
  grid-template-columns: 42px 1fr;
}

.control__input {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 24px;
  margin: 0;
  grid-template-rows: 1/2;
  grid-row: 1/2;
  opacity: 0;
}

.control--radio .control__lead {
  grid-template-columns: 24px 1fr;
}

.control--checkbox .control__lead {
  grid-template-columns: 42px 1fr;
}

.control__lead {
  grid-column: 1/-1;
  grid-row: 1/2;
  display: grid;
  gap: 10px;
  align-items: center;
}
.control__lead::before, .control__lead::after {
  content: "";
  height: 24px;
  grid-column: 1/2;
  grid-row: 1/2;
}
.control--radio .control__lead::before {
  border: 2px solid #7859CF;
  border-radius: 50%;
}
.control--radio .control__input:checked + .control__lead::after {
  background-clip: padding-box;
  background-color: #7859CF;
  border: 4px solid transparent;
  border-radius: 50%;
}
.control--checkbox .control__lead::before {
  width: 42px;
  border-radius: 12px;
  background-color: #BDBDBD;
}
.control--checkbox .control__input:checked + .control__lead::before {
  background-color: #7859CF;
}
.control--checkbox .control__lead::after {
  width: 20px;
  height: 20px;
  background-clip: padding-box;
  background-color: #ffffff;
  border: 1.5px solid transparent;
  border-radius: 50%;
}
.control--checkbox .control__input:checked + .control__lead::after {
  margin-left: auto;
}

.control--radio:hover .control__lead::before,
.control--radio:focus .control__lead::before,
.control--checkbox:hover .control__lead::before,
.control--checkbox:focus .control__lead::before {
  background-color: rgba(120, 89, 207, 0.25);
}

