.promo {
  padding: 20px 0 60px;
  text-align: center;
  background-color: #F3EBE1;
  overflow: hidden;
}

.peppermint-slides {
  display: flex;
}

.promo__container {
  position: relative;
  display: grid;
}

.promo__btns {
  position: absolute;
  top: 50%;
  display: flex;
  justify-content: space-between;
  width: 100%;
  transform: translateY(-50%);
}

@media (max-width: 1300px) {
  .promo__item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
@media (min-width: 1300px) {
  .promo__item {
    display: grid;
    grid-template-columns: 600px 650px;
    column-gap: 30px;
    align-items: center;
  }
}

@media (min-width: 1300px) {
  .promo__content {
    text-align: left;
  }
}

.promo__picture,
.promo__image {
  width: 280px;
}
@media (min-width: 768px) {
  .promo__picture,
  .promo__image {
    order: 2;
    width: 476px;
    height: 476px;
  }
}
@media (min-width: 1300px) {
  .promo__picture,
  .promo__image {
    width: 600px;
    height: 600px;
    order: 0;
  }
}

.promo__header {
  margin: 0;
  margin-bottom: 10px;
  font-size: 36px;
  font-weight: 700;
  line-height: 48px;
  letter-spacing: 2px;
}
@media (min-width: 768px) {
  .promo__header {
    margin-bottom: 16px;
    font-size: 56px;
    line-height: 80px;
  }
}

.promo__text {
  margin: 0;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .promo__text {
    font-size: 20px;
    line-height: 32px;
  }
}
@media (min-width: 1300px) {
  .promo__text {
    margin-bottom: 40px;
  }
}

.promo__old-price {
  font-size: 16px;
  line-height: 32px;
  color: #7859CF;
}

.promo__new-price {
  font-size: 20px;
  line-height: 32px;
  font-weight: 700;
}

.promo__btn {
  width: 80px;
  color: #7859CF;
  background-color: transparent;
  border: solid 1px red;
  cursor: pointer;
  background-image: url("../img/icons/arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.promo__btn--next {
  transform: rotate(180deg);
}

.promo__toggles, .peppermint-dots {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.promo__toggle, .peppermint-dots > li {
  width: 62px;
  height: 4px;
  margin: 0 4px;
  padding: 0;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}

.promo__toggle--current, .peppermint-dots > .peppermint-active-dot {
  background-color: #7859CF;
}

.slider {
  display: flex;
}
@media (max-width: 767px) {
  .slider {
    max-width: 320px;
  }
}
@media (min-width: 767px) {
  .slider {
    max-width: 1360px;
  }
}

.range__scale {
  position: relative;
  height: 2px;
  margin-bottom: 35px;
  background-color: #E2E2E2;
}

.range__bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 4px;
  background-color: #7859CF;
  height: 100%;
}

.range__toggle {
  position: absolute;
  width: 24px;
  height: 24px;
  background-color: #ffffff;
  border: solid 2px #7859CF;
  border-radius: 50%;
  cursor: pointer;
}
.range__toggle:focus, .range__toggle:hover {
  background-color: #F0EBFF;
}
.range__toggle:active {
  background-color: #7859CF;
}
.range__toggle:disabled {
  border-color: #BDBDBD;
}

.range__toggle--min {
  top: -11px;
  left: 0px;
}

.range__toggle--max {
  top: -11px;
  right: 40px;
}

.range__fields {
  display: flex;
  justify-content: space-between;
}

.range__input {
  width: 118px;
  height: 40px;
  padding: 8px 10px;
  font: inherit;
  text-align: center;
  background-color: #ffffff;
  border: solid 2px #7859CF;
  border-radius: 4px;
  appearance: textfield;
}
@media (min-width: 768px) {
  .range__input {
    width: 87px;
  }
}
.range__input:focus, .range__input:hover, .range__input:active {
  border-color: #593CAC;
}

.range-input::-webkit-outer-spin-button,
.range-input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

.site-menu {
  text-align: center;
}
@media (max-width: 767px) {
  .site-menu {
    border-top: 1px solid #F0EBFF;
  }
}

@media (min-width: 768px) {
  .site-menu__item {
    width: 25%;
  }
  .site-menu__item--active {
    position: relative;
  }
  .site-menu__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background-color: #7859CF;
  }
}

@media (min-width: 768px) {
  .site-menu__item a[href]:hover, .site-menu__item a[href]:focus {
    color: #7859CF;
  }
  .site-menu__item a[href]:active {
    background-color: #F0EBFF;
    color: #7859CF;
  }
}

.site-menu a {
  position: relative;
  display: block;
  padding: 20px 22px 21px;
  font-size: 12px;
  line-height: 16px;
  color: #333333;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #ffffff;
  border-bottom: 1px solid #F0EBFF;
}
@media (min-width: 768px) {
  .site-menu a {
    padding: 32px 20px;
    border: none;
  }
}

.social {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.social__item {
  width: 40px;
  height: 40px;
  margin-right: 20px;
}

.social__item:nth-child(3n) {
  margin-right: 0;
}

.social__link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
}
.social__link:hover, .social__link:focus {
  background-color: #F0EBFF;
}
.social__link:active {
  background-color: rgba(120, 89, 207, 0.25);
}
.social__link:hover .social__icon, .social__link:focus .social__icon, .social__link:active .social__icon {
  fill: #7859CF;
}

.social__icon {
  fill: #BDBDBD;
}

@media (max-width: 767px) {
  .user-list {
    width: 120px;
    margin-left: auto;
  }
}

.user-list__link {
  position: relative;
  display: block;
  padding: 22px;
  font-size: 12px;
  line-height: 16px;
  color: #333333;
  text-transform: uppercase;
  text-decoration: none;
}
.user-list__link span {
  display: none;
}
.user-list__link:hover, .user-list__link:focus {
  color: #7859CF;
}
.user-list__link:active {
  background-color: #F0EBFF;
}
.user-list__link:hover .user-list__icon, .user-list__link:focus .user-list__icon, .user-list__link:active .user-list__icon {
  fill: #7859CF;
}
@media (min-width: 768px) {
  .user-list__link {
    padding: 32px 22px;
  }
}
@media (min-width: 1300px) {
  .user-list__link {
    padding-left: 44px;
  }
  .user-list__link span {
    display: block;
  }
}

@media (min-width: 1300px) {
  .user-list__icon {
    position: absolute;
    top: 30px;
    left: 20px;
    width: 16px;
    height: 16px;
    fill: #333333;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}