.logo__image {
    width: 36px;

    @media (min-width: $breakpoint-medium) {
        width: 82px;
    }

    @media (min-width: $breakpoint-large) {
        width: 170px;
    }
}

.logo__image--footer {
    width: 82px;

    @media (min-width: $breakpoint-large) {
        width: 170px;
    }
}

.logo__sign {
    display: none;
    color: $color-black;
    font-size: 12px;
    line-height: 16px;
    text-decoration: none;

    @media (min-width: $breakpoint-large) {
        display: block;

    }
}

.logo {
    text-decoration: none;
}

.logo--header {
    @media (min-width: $breakpoint-large) {
        display: grid;
        grid-template-columns: 170px 130px;
        column-gap: 20px;
        align-items: center;
    }
}

.logo--footer {
    @media (min-width: $breakpoint-large) {
        display: grid;
        grid-template-rows: 36px auto;
        row-gap: 10px;
    }
}