.control {
    position: relative;
    z-index: 0;
    display: grid;
    gap: 10px;
    font-size: 16px;
    line-height: 24px;
    cursor: pointer;
}

.control--radio {
    grid-template-columns: 24px 1fr;
}

.control--checkbox {
    grid-template-columns: 42px 1fr;
}

.control__input {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 24px;
    margin: 0;
    grid-template-rows: 1 / 2;
    grid-row: 1 / 2;
    opacity: 0;
}

.control--radio .control__lead {
    grid-template-columns: 24px 1fr;
}

.control--checkbox .control__lead {
    grid-template-columns: 42px 1fr;
}

.control__lead {
    grid-column: 1 / -1;
    grid-row: 1 / 2;
    display: grid;
    gap: 10px;
    align-items: center;

    &::before,
    &::after {
        content: "";
        height: 24px;
        grid-column: 1 / 2;
        grid-row: 1 / 2;
    }

    .control--radio &::before {
        border: 2px solid $color-lilac;
        border-radius: 50%;
    }

    .control--radio .control__input:checked + &::after {
        background-clip: padding-box;
        background-color: $color-lilac;
        border: 4px solid transparent;
        border-radius: 50%;
    }

    .control--checkbox &::before {
        width: 42px;
        border-radius: 12px;
        background-color: #BDBDBD;
    }

    .control--checkbox .control__input:checked + &::before {
        background-color: $color-lilac;
    }

    .control--checkbox &::after {
        width: 20px;
        height: 20px;
        background-clip: padding-box;
        background-color: $color-white;
        border: 1.5px solid transparent;
        border-radius: 50%;
    }

    .control--checkbox .control__input:checked + &::after {
        margin-left: auto;
    }
}

.control--radio:hover .control__lead::before,
.control--radio:focus .control__lead::before,
.control--checkbox:hover .control__lead::before,
.control--checkbox:focus .control__lead::before {
    background-color: $art-opacity-lilac;
}