.range__scale {
    position: relative;
    height: 2px;
    margin-bottom: 35px;
    background-color: #E2E2E2;
}

.range__bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 4px;
    background-color: $color-lilac;
    height: 100%;
}

.range__toggle {
    position: absolute;
    width: 24px;
    height: 24px;
    background-color: $color-white;
    border: solid 2px $color-lilac;
    border-radius: 50%;
    cursor: pointer;

    &:focus,
    &:hover {
        background-color: #F0EBFF;
    }

    &:active {
        background-color: $color-lilac;
    }

    &:disabled {
        border-color: #BDBDBD;
    }
}

.range__toggle--min {
    top: -11px;
    left: 0px;
}

.range__toggle--max {
    top: -11px;
    right: 40px;
}

.range__fields {
    display: flex;
    justify-content: space-between;
}

.range__input {
    width: 118px;
    height: 40px;
    padding: 8px 10px;
    font: inherit;
    text-align: center;
    background-color: $color-white;
    border: solid 2px $color-lilac;
    border-radius: 4px;
    appearance: textfield;

    @media (min-width: $breakpoint-medium) {
        width: 87px;
    }

    &:focus,
    &:hover,
    &:active {
        border-color: #593CAC;
    }
}

.range-input::-webkit-outer-spin-button,
.range-input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}