.site-menu {
    text-align: center;

    @media (max-width: $breakpoint-small-down) {
        border-top: 1px solid #F0EBFF;
    }
}

.site-menu__item {
    @media (min-width: $breakpoint-medium) {
        width: 25%;
        &--active {
            position: relative;

            &::after {
                content: "";
                position: absolute;
                right: 0;
                bottom: 0;
                left: 0;
                height: 2px;
                background-color: $color-lilac;
            }
        }
    }
}

.site-menu__item a[href] {
    @media (min-width: $breakpoint-medium) {
        &:hover,
        &:focus {
            color: #7859CF;
        }

        &:active {
            background-color: #F0EBFF;
            color: #7859CF;
        }
    }
}

.site-menu a {
    position: relative;
    display: block;
    padding: 20px 22px 21px;
    font-size: 12px;
    line-height: 16px;
    color: $color-black;
    text-transform: uppercase;
    text-decoration: none;
    background-color: $color-white;
    border-bottom: 1px solid #F0EBFF;

    @media (min-width: $breakpoint-medium) {
        padding: 32px 20px;
        border: none;
    }
}
