.contacts__address {
    font-style: normal;
}

.contacts__phone {
    position: relative;
    display: block;
    margin-top: 0px;
    padding-left: 20px;
    color: inherit;
    text-decoration: none;
}

.contacts__text {
    position: relative;
    margin-top: 0px;
    margin-bottom: 16px;
    padding-left: 20px;
}

.contacts__text::before {
    content: "";
    display: block;
    width: 13px;
    height: 16px;
    background-image: url("../img/icons/location.svg");
    position: absolute;
    top: 4px;
    left: 0;
}

.contacts__phone::before {
    content: "";
    display: block;
    width: 13px;
    height: 16px;
    background-image: url("../img/icons/phone.svg");
    position: absolute;
    top: 4px;
    left: 0;
}

.contacts .page-footer__heading {
    margin-bottom: 20px;
}

.contacts__text br {
    @media (max-width: $breakpoint-large) {
        display: none;
    }
}