.user-list {
    @media (max-width: $breakpoint-small-down) {
        width: 120px;
        margin-left: auto;
    }
}

.user-list__link {
    position: relative;
    display: block;
    padding: 22px;
    font-size: 12px;
    line-height: 16px;
    color: $color-black;
    text-transform: uppercase;
    text-decoration: none;


    span {
        display: none;
    }

    &:hover,
    &:focus {
        color: $color-lilac;
    }

    &:active {
        background-color: #F0EBFF;
    }

    &:hover .user-list__icon,
    &:focus .user-list__icon,
    &:active .user-list__icon {
        fill: $color-lilac;
    }

    @media (min-width: $breakpoint-medium) {
        padding: 32px 22px;
    }

    @media (min-width: $breakpoint-large) {
        padding-left: 44px;

        span {
            display: block;
        }
    }
}

.user-list__icon {
    @media (min-width: $breakpoint-large) {
        position: absolute;
        top: 30px;
        left: 20px;
        width: 16px;
        height: 16px;
        fill: $color-black;
    }
}

