.developer__text {
    padding-right: 5px;
}

.developer {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    line-height: 16px;
    text-decoration: none;
    color: #BDBDBD;

    &:hover,
    &:focus {
        color: $color-lilac;
    }
}

.developer__image {
    fill: #BDBDBD;
}

.developer:hover .developer__image,
.developer:focus .developer__image {
    fill: $color-lilac;
} 