.page-header {
    color: $color-black;
    background-color: $color-white;
}

.page-header__nav {
    width: auto;
    height: 60px;
    display: grid;
    align-items: center;

    @media (max-width: $breakpoint-small-down) {
        width: 320px;
        margin: 0 auto;
        padding: 0px 20px;
        grid-template-columns: 32px 1fr 40px;
        align-items: center;
    } 

    @media (min-width: $breakpoint-medium) {
        width: 708px;
        height: 80px;
        margin: 0 auto;
        gap: 18px;
        grid-template-columns: 82px 1fr 120px;
    }

    @media (min-width: $breakpoint-large) {
        width: 1220px;
        grid-template-columns: 320px 1fr 262px;
    }
}

.page-header__user-list {
    @media (min-width: $breakpoint-medium) {
        order: 2;
    }

    @media (min-width: $breakpoint-medium) {
        margin-left: auto;
    }
}

.page-header__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
}

.page-header__toggler {
    width: 60px;
    height: 60px;
    display: none;
    background-color: transparent;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-image: url("../img/icons/burger.svg");
    border: none;

    &:hover,
    &:active {
        background-color: #F0EBFF;
    }

    @media (max-width: $breakpoint-small-down) {
        .page-header__nav--closed & {
            display: block;
        }

        .page-header__nav--opened & {
            display: block;
            background-image: url("../img/icons/close.svg");
           
        }
    }

    @media (min-width: $breakpoint-medium) {
        display: none;
    }
}

.page-header__site-menu {
    background-color: $color-white;

    @media (max-width: $breakpoint-small-down) {
        .page-header__nav--closed & {
            display: none;
        }

        .page-header__nav--opened & {
            position: absolute;
            right: 0;
            left: 0;
            top: 60px;
            z-index: 2;
            display: block;
        }
    }

    @media (min-width: $breakpoint-medium) {
        margin-right: auto;
        margin-left: auto;
    }
}