.catalog {
    padding: 50px 0px 44px;
    background-color: #F8F8F8;

    @media (min-width: $breakpoint-medium) {
        padding: 80px 0px 90px;
    }

    @media (min-width: $breakpoint-large) {
        padding: 100px 0px 120px;
    }
}

.catalog__buttons {
    @media (max-width: $breakpoint-small-down) {
        display: flex;
        flex-direction: column;
    }

    @media (min-width: $breakpoint-medium) {
        display: grid;
        grid-template-columns: 137px 120px;
        gap: 20px;
        grid-column: 1 / -1;
        justify-self: center;
    }

    @media (min-width: $breakpoint-large) {
        grid-template-columns: 149px 149px;
    }
}

.catalog__filter {
    margin-bottom: 26px;

    @media (min-width: $breakpoint-medium) and (max-width: $breakpoint-large) {
        margin-bottom: 34px;
        display: grid;
        grid-template-columns: 223px 230px 215px;
        column-gap: 30px;
        row-gap: 40px;
    }
}

.catalog__container--desktop {
    @media (min-width: $breakpoint-large) {
        display: grid;
        grid-template-columns: 318px 1011px;
        column-gap: 30px;
    }
}

.catalog__header {
    margin: 0;
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 36px;
    font-weight: 700;
    text-align: center;

    @media (min-width: $breakpoint-medium) {
        font-size: 42px;
        line-height: 64px;
    }

    @media (min-width: $breakpoint-large) {
        margin-bottom: 40px;
    }
}
