.page-footer {
    padding: 20px 0 30px;

    @media (min-width: $breakpoint-medium) {
        padding: 40px 0;
    }
}

.page-footer__container {
    @media (min-width: $breakpoint-medium) {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-areas: 
        "social social social"
        "store media contacts"
        "developer developer developer";
        gap: 30px;
    }

    @media (min-width: $breakpoint-large) {
        grid-template-areas: 
        "social store media contacts"
        "developer developer developer developer";
    }
}

.page-footer__social {
    margin: 0;
}

.page-footer__heading {
    margin: 0;
    margin-bottom: 12px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    letter-spacing: 2px;
    color: $color-lilac;
}

.page-footer__grid {
    @media (max-width: $breakpoint-small-down) {
        padding: 20px 0;
    }
}

.page-footer__grid--social {
    display: flex;
    justify-content: space-between;
    @media (max-width: $breakpoint-small-down) {
        padding-bottom: 20px;
    }

    @media (min-width: $breakpoint-medium) {
        grid-area: social;
    }

    @media (min-width: $breakpoint-large) {
        flex-direction: column;
    }
}

.page-footer__grid--store {
    @media (min-width: $breakpoint-medium) {
        grid-area: store;
    }
}

.page-footer__grid--media {
    @media (min-width: $breakpoint-medium) {
        grid-area: media;
    }
}

.page-footer__grid--contacts {
    @media (min-width: $breakpoint-medium) {
        grid-area: contacts;
    }
}

.page-footer__developer {
    @media (max-width: $breakpoint-small-down) {
        margin-top: 20px;
    }

    @media (min-width: $breakpoint-medium) {
        grid-area: developer;
    }
}

.page-footer__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.page-footer__item:not(:last-child) {
    margin-bottom: 12px;
}

.page-footer__link {
    display: block;
}