.card__wrapper {
    padding-bottom: 4px;

    @media (min-width: $breakpoint-medium) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
    }

    @media (min-width: $breakpoint-large) {
        grid-template-columns: repeat(3, 1fr);
    }
}

.card {
    display: grid;
    grid-template-rows: 188px auto;
    grid-template-areas: 
        "img"
        "header"
        "description"
        "footer";
    padding: 40px 20px 20px 20px;
    background-color: $color-white;

    @media (min-width: $breakpoint-medium) {
        padding: 50px 30px 30px 30px;
    }

    & + & {
        @media (max-width: $breakpoint-small-down) {
            margin-top: 20px;
        }
    }
}

.card__footer {
    margin-top: 16px;
    grid-area: footer;
    display: grid;
    grid-template-columns: 1fr 141px;
    grid-template-rows: 36px;
    column-gap: 15px;

    @media (min-width: $breakpoint-medium) {
        margin-top: 20px;
    }
}

.card__link--image {
    grid-area: img;
    align-self: center;
    justify-self: center;
    width: auto;
}

.card__link--header {
    grid-area: header;
}

.card__text {
    margin: 0;
    margin-top: 4px;
    grid-area: description;

    @media (min-width: $breakpoint-medium) {
        margin-top: 10px;
    }
}

.card__title {
    margin: 0;
    margin-top: 20px;
    font-size: 18px;
    line-height: 32px;
    font-weight: 700;
    color: $color-black;

    @media (min-width: $breakpoint-medium) {
        margin-top: 30px;
        font-size: 24px;
        line-height: 36px;
    }
}

.card__price {
    font-size: 20px;
    line-height: 32px;
    font-weight: 600;
}

.card__button {
    padding: 7px;
    padding-left: 40px;
    font-size: 14px;
    line-height: 20px;
    background-image: url("../img/icons/cart-btn.svg");
    background-repeat: no-repeat;
    background-position: 20px center;
}

.card__link {
    text-decoration: none;
}

.card:hover {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);;
}