.field-group {
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
    border: none;

    @media (min-width: $breakpoint-medium) and (max-width: $breakpoint-large) {
        margin-bottom: 0;
    }
}

.field-group__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.field-group__item {
    & + & {
        margin-top: 20px;
    }
}

.field-group__title {
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 24px;
    font-weight: 600;
}