.pagination {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    margin-top: 40px;
    padding: 0;
    list-style-type: none;

    @media (min-width: $breakpoint-medium) {
        margin-top: 70px;
    }

    @media (min-width: $breakpoint-large) {
        margin-top: 60px;
        flex-direction: row-reverse;
    }
}

.pagination__item:not(:last-child) {
    margin-right: 20px;
}

@media (min-width: $breakpoint-large) {
    .pagination__item:not(:first-child) {
        margin-right: 20px;
    }
}


.pagination__link {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 40px;
    height: 40px;
    padding: 8px;
    text-decoration: none;
    background-color: $color-white;
    font-size: 16px;
    line-height: 24px;
    color: #7859CF;
    border-radius: 4px;
    background-color: $color-white;

    @media (max-width: $breakpoint-small-down) {
        span {
            opacity: 0;
            display: none;
        }
    }

    &:hover,
    &:focus {
        background-color: #9070EC;
        color: $color-white;
    }

    &:active {
        background-color: #593CAC;
        color: $color-white;
    }

    &:disabled {
        background: #E2E2E2;
        color: $color-white;
    }

    &:hover .pagination__icon,
    &:focus .pagination__icon,
    &:active .pagination__icon {
        fill: $color-white;
    }
}

.pagination__icon {
    fill: $color-lilac;

    @media (min-width: $breakpoint-medium) {
        display: none;
    }
}

.pagination__item--current .pagination__link {
    background-color: #F8F8F8;
    color: $color-black;
    font-weight: 600;
}