.container {
    width: 280px;
    margin-right: auto;
    margin-left: auto;

    @media (min-width: $breakpoint-medium) {
        width: 728px;
    }

    @media (min-width: $breakpoint-large) {
        width: 1360px;
    }
}

.container__label {
    display: block;
    margin-bottom: 4px;
    font-size: 12px;
    line-height: 16px;
    font-weight: 700;
    letter-spacing: 4px;
    text-transform: uppercase;
    color: $color-lilac;
    text-align: center;
}