.sorting {
    font-size: 16px;
    line-height: 24px;
    font-weight: 600;
}

.sorting__wrapper {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 280px;
    padding: 10px 0;
    margin-bottom: 10px;
    
    @media (min-width: $breakpoint-medium) {
        margin-left: auto;
        margin-bottom: 16px;
    }
}

.sorting__control {
    width: 165px;
    padding: 4px 10px;
    padding-right: 30px;
    font: inherit;
    color: $color-lilac;
    font-weight: 600;
    background-color: transparent;
    border: none;
    background-image: url("../img/icons/arrow-down.svg");
    background-repeat: no-repeat;
    background-position: right 15px center;
    appearance: none;
    cursor: pointer;

    &:hover,
    &:focus {
        background-color: #F0EBFF;
    }

    &:active {
        background-image: url("../img/icons/arrow-up.svg");
    }
}