.social {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.social__item {
    width: 40px;
    height: 40px;
    margin-right: 20px;
}

.social__item:nth-child(3n) {
    margin-right: 0;
}

.social__link {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px;

    &:hover,
    &:focus {
        background-color: #F0EBFF;
    }

    &:active {
        background-color: $art-opacity-lilac;
    }

    &:hover .social__icon,
    &:focus .social__icon,
    &:active .social__icon {
        fill: $color-lilac;
    }
}

.social__icon {
    fill: #BDBDBD;
}