.button {
    padding: 8px 20px;
    display: inline-block;
    border: none;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    background-color: $color-lilac;
    color: $color-white;
    text-decoration: none;
    text-align: center;
    border-radius: 4px;

    &:hover,
    &:focus {
        background-color: #9070EC;
    }

    &:active {
        background-color: #593CAC;
    }

    &:disabled {
        background-color: #BDBDBD;
    }
}

.button-light {
    background-color: $color-white;
    color: $color-lilac;
    padding: 8px 20px;

    &:hover,
    &:focus,
    &:active,
    &:disabled {
        color: $color-white;
    }
}

.button + .button {
    @media (max-width: $breakpoint-small-down) {
        margin-top: 20px;
    }
}